    .App {}
    
    .App-logo {
        height: 40vmin;
        pointer-events: none;
    }
    
    @media (prefers-reduced-motion: no-preference) {
        .App-logo {
            animation: App-logo-spin infinite 20s linear;
        }
    }
    
    .App-header {
        background-color: #282c34;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        font-size: calc(10px + 2vmin);
        color: white;
    }
    
    .App-link {
        color: #61dafb;
    }
    
    @keyframes App-logo-spin {
        from {
            transform: rotate(0deg);
        }
        to {
            transform: rotate(360deg);
        }
    }
    
    body {
        background-color: #282c34;
        color: white;
    }
    
    .paginationBttns {
        height: 40px;
        list-style: none;
        display: flex;
        justify-content: center;
    }
    
    .paginationBttns a {
        padding-top: 3px;
        padding-right: 6px;
        padding-bottom: 3px;
        padding-left: 6px;
        margin: 8px;
        border-radius: 30px;
        border: 1px solid #FFE4E1;
        color: #FFE4E1;
        cursor: pointer;
    }
    
    .paginationBttns a:hover {
        color: blue;
        background-color: #FFE4E1;
    }
    
    .paginationActive a {
        color: blue;
        background-color: #FFE4E1;
    }
    
    .paginationDisabled a {
        color: grey;
        background-color: 0;
    }
    
    .linkMenu {
        text-decoration: none;
        padding: 20px;
        color: #696969;
        text-align: center;
        margin: 4px 2px;
        transition: 0.3s;
    }
    
    .linkMenu:hover {
        border-top: 1px solid #61dafb;
        color: #61dafb;
    }
    
    .fade-appear .fade-enter {
        opacity: 0;
        z-index: 1;
    }
    
    .fade-appear-active .fade-enter.fade-appear-active {
        opacity: 1;
        transition: opacity 300ms linear 150ms;
    }
    
    .fade-exit {
        opacity: 1;
    }
    
    .fade-exit.fade-exit-active {
        opacity: 0;
        transition: opacity 150ms linear;
    }
    
    .float-left {
        margin-left: auto;
    }
    
    .paginationDisabled,
    .paginationDisabled a {
        pointer-events: auto! important;
        cursor: not-allowed! important;
    }
    
    .inputSearch {
        background: white;
        border-radius: 10px;
    }